export default angular.module('deskworks.NexkeyCenter', [
  'ngResource',
  'deskworks.config',
])

  .factory('NexkeyCenter', function($resource, deskworksConfig) {

    const res = $resource(deskworksConfig.getApiAbsUrl('/nexkey/centers/:id'), {}, {
      update: { method: 'PUT' },
    });

    return {
      query: () => res.query({}).$promise,

      get: (params) => res.get(params).$promise,

      save: center => res.update({ id: center.id }, center).$promise,
    };
  });
