export default angular.module('deskworks.NexkeyUnit', [
  'ngResource',
  'deskworks.config',
])

  .factory('NexkeyUnit', function($resource, deskworksConfig) {

    const res = $resource(deskworksConfig.getApiAbsUrl('/nexkey/units/:id'), {}, {
      update: { method: 'PUT' },
    });

    return {
      get: (params) => res.get(params).$promise,

      save: unit => res.update({ id: unit.id }, unit).$promise,
    };
  });
