/**
 * Integrations Setup -> Nexkey Settings directive
 */
export default angular.module('deskworks.nexkeySettingsDirective', [
  'debounce',
  'deskworks.center.service',
  'deskworks.NexkeyCenter',
])

  .directive('nexkeySettings', function() {
    const templateUrl = 'settings/nexkey-settings.tpl.html';
    return {
      templateUrl,
      restrict: 'E',
      scope: true,
      controller: 'NexkeySettingsCtrl as $ctrl',
    };
  })

  .controller('NexkeySettingsCtrl', function($q, debounce, dwAlerts, helper, state, NexkeyCenter) {
    const $ctrl = this;

    $ctrl.$onInit = () => {
      $ctrl.load();
    };

    /**
     * Load settings
     */
    $ctrl.load = () => {
      $ctrl.spin = true;

      return NexkeyCenter.query()
        .then(result => {
          $ctrl.nexkeyCenters = result;
          return result;
        })
        .finally(() => { $ctrl.spin = false; });
    };

    /**
     * Save center
     */
    $ctrl.saveCenter = debounce(saveCenter, 500);
    function saveCenter(center, form) {
      if (form && form.$invalid) return;
      center.$$spin = true;
      return NexkeyCenter.save(center)
        .finally(() => { center.$$spin = false; })
        .catch(err => dwAlerts.error(err, 'Failed to save Nexkey center.'));
    }
  });
