/**
 * Nexkey locks directive
 */
export default angular.module('deskworks.nexkeyLocksDirective', [
  'deskworks.NexkeyCenter',
  'deskworks.NexkeyUnit',
])

  .directive('nexkeyLocks', function() {
    const templateUrl = 'locks/nexkey-locks.tpl.html';
    return {
      templateUrl,
      restrict: 'E',
      scope: true,
      controller: 'NexkeyLocksCtrl as $ctrl',
      bindToController: {
        centerId: '<?',
        unitId: '<?',
        save: '=',
      },
    };
  })

  .controller('NexkeyLocksCtrl', function($q, dwAlerts, helper, state, NexkeyCenter, NexkeyUnit) {
    const $ctrl = this;

    $ctrl.$onInit = () => {
      $ctrl.load();
      $ctrl.save = save;
    };

    /**
     * Load settings
     */
    $ctrl.load = () => {
      const promises = [
        NexkeyCenter.get({ id: $ctrl.centerId }).catch(err => dwAlerts.error(err, 'Failed to get Nexkey center.')),
      ];
      if ($ctrl.unitId) {
        promises.push(NexkeyUnit.get({ id: $ctrl.unitId }).catch(err => dwAlerts.error(err, 'Failed to get Nexkey unit.')));
      }

      return $q.all(promises)
        .then(results => {
          $ctrl.center = results[0];
          $ctrl.unit = results[1];
          $ctrl.settings = $ctrl.unit || $ctrl.center;
          $ctrl.settings.lockIds = $ctrl.settings.lockIds.join(', ');
        });
    };

    /**
     * Save settings
     */
    function save() {
      if ($ctrl.unitId) {
        const unit = { ...$ctrl.unit, lockIds: $ctrl.unit.lockIds.split(',').map(v => v.trim()) };
        return NexkeyUnit.save(unit).catch(err => dwAlerts.error(err, 'Failed to save Nexkey unit.'));
      }
      const center = { ...$ctrl.center, lockIds: $ctrl.center.lockIds.split(',').map(v => v.trim()) };
      return NexkeyCenter.save(center).catch(err => dwAlerts.error(err, 'Failed to save Nexkey center.'));
    }
  });
